/* Navbar.css for the navbar */

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
  }
  
  .navbar a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    margin-right: 20px;
  }
  
  .navbar a:hover {
    text-decoration: underline;
  }
  
 
  